"use client";

import { useState } from "react";
import {
  Calendar,
  CalendarClock,
  Wallet,
  MessageSquare,
} from "lucide-react";
import Link from "next/link";

export default function PatientDashboardCards() {
  const [showChat, setShowChat] = useState(false);

  return (
    <>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-8">
        {/* Book Appointment */}
        <Link
          href="/bookappointment"
          className="block cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
        >
          <CalendarClock className="w-10 h-10 text-blue-600" />
          <div>
            <h3 className="text-xl font-semibold text-gray-800">
              Book Appointment
            </h3>
          </div>
        </Link>

        {/* My Appointments */}
        <Link
          href="/patientappointments"
          className="block cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
        >
          <Calendar className="w-10 h-10 text-blue-600" />
          <div>
            <h3 className="text-xl font-semibold text-gray-800">
              My Appointments
            </h3>
          </div>
        </Link>

        {/* My Payments */}
        <Link
          href="/patientpayments"
          className="block cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
        >
          <Wallet className="w-10 h-10 text-yellow-600" />
          <div>
            <h3 className="text-xl font-semibold text-gray-800">
              My Payments
            </h3>
          </div>
        </Link>

        {/* Chat Support (No Navigation, Just Pop-up) */}
        <div
          onClick={() => setShowChat(true)}
          className="block cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
        >
          <MessageSquare className="w-10 h-10 text-indigo-600" />
          <div>
            <h3 className="text-xl font-semibold text-gray-800">
              Chat Support
            </h3>
            <p className="text-gray-600">5 active chats</p>
          </div>
        </div>
      </div>

      {/* Chat Support Dialog */}
      {showChat && (
        <div className="fixed bottom-4 left-4 w-full max-w-sm bg-white border border-gray-300 rounded-xl shadow-lg z-50">
          <div className="flex justify-between items-center p-4 border-b">
            <h3 className="text-lg font-semibold text-gray-800">Chat Support</h3>
            <div className="flex gap-2">
              <button
                onClick={() => setShowChat(false)}
                className="text-gray-600 hover:text-gray-900"
              >
                âœ–
              </button>
            </div>
          </div>
          <div className="p-4">
            <p className="text-gray-600 text-sm mb-2">How can we help you today?</p>
            <textarea
              placeholder="Type your message..."
              className="w-full border border-gray-300 p-2 rounded-md"
              rows={3}
            />
            <button className="mt-2 bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700">
              Send
            </button>
          </div>
        </div>
      )}
    </>
  );
}
